//    QUB  USDT
import {uniswapRewardsUsdt} from "@/util/contract/uniswap-rewards-usdt"
import store from '../../store'
import app from "@/main";

export default {
    namespaced: true,
    state: {
        earned: 0,
        balance: 0,
    },
    getters: {
        earned: state => state.earned,
        balance: state => state.balance,
    },
    mutations: {
        earnedSuccess(state, payload) {
            state.earned = payload
        },
        balanceOfSuccess(state, payload) {
            state.balance = payload
        },
        earnedError(state, payload) {
            state.earned = 0
            console.log('earned error')
            console.log(payload)
        },
        balanceOfError(state, payload) {
            state.balance = 0
            console.log('balanceOf error')
            console.log(payload)
        },
        getRewardError(state, payload) {
            console.log('getReward error')
            console.log(payload)
        },
        withdrawError(state, payload) {
            console.log('withdraw error')
            console.log(payload)
        }
    },
    actions: {
        getEarned({commit}) {
            var contract = new store.state.provider.web3.eth.Contract(uniswapRewardsUsdt.abi, uniswapRewardsUsdt.address)

            contract.methods.earned(store.state.provider.account).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('earnedError', error)
                }else{
                    commit('earnedSuccess', data)
                }
            })
        },
        getBalanceOf({commit}) {
            var contract = new store.state.provider.web3.eth.Contract(uniswapRewardsUsdt.abi, uniswapRewardsUsdt.address)

            contract.methods.balanceOf(store.state.provider.account).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('balanceOfError', error)
                }else{
                    commit('balanceOfSuccess', data)
                }
            })
        },
        txStake({ commit }, {amount}) {
            if (store.state.provider.web3 == null) {store.commit('setErrorMessage', app.$i18n.t('general.you-need-connect')); return }

            var totalAmount = store.state.provider.web3.utils.toWei(amount.toString(), 'ether')

            if (totalAmount < 0) {
                totalAmount = 0
            }

            var contract = new store.state.provider.web3.eth.Contract(uniswapRewardsUsdt.abi, uniswapRewardsUsdt.address)

            contract.methods.stake(totalAmount).send({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    if (error.message != undefined) {
                        store.commit('setErrorMessage',error.message)
                    }else{
                        commit('stakeError', error)
                    }
                }else{
                    store.commit('transactionCreated', data)
                }
            })
        },
        txGetReward({commit}) {
            if (store.state.provider.web3 == null) {store.commit('setErrorMessage', app.$i18n.t('general.you-need-connect')); return }

            var contract = new store.state.provider.web3.eth.Contract(uniswapRewardsUsdt.abi, uniswapRewardsUsdt.address)

            contract.methods.getReward().send({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    if (error.message != undefined) {
                        store.commit('setErrorMessage',error.message)
                    }else{
                        commit('getRewardError', error)
                    }
                } else {
                    store.commit('transactionCreated', data)
                }
            })
        },
        txExit({commit}) {
            if (store.state.provider.web3 == null) {store.commit('setErrorMessage', app.$i18n.t('general.you-need-connect')); return }

            var contract = new store.state.provider.web3.eth.Contract(uniswapRewardsUsdt.abi, uniswapRewardsUsdt.address)

            contract.methods.exit().send({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    if (error.message != undefined) {
                        store.commit('setErrorMessage',error.message)
                    }else{
                        commit('exitError', error)
                    }
                }else{
                    store.commit('transactionCreated', data)
                }
            })
        },
        txWithdraw({commit}, {amount}) {
            if (store.state.provider.web3 == null) {store.commit('setErrorMessage', app.$i18n.t('general.you-need-connect')); return }

            var totalAmount = store.state.provider.web3.utils.toWei(amount.toString(), 'ether')

            if (totalAmount < 0) {
                totalAmount = 0
            }

            var contract = new store.state.provider.web3.eth.Contract(uniswapRewardsUsdt.abi, uniswapRewardsUsdt.address)

            contract.methods.withdraw(totalAmount).send({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('withdrawError', error)
                }else{
                    store.commit('transactionCreated', data)
                }
            })
        },
    }
}