<script>
import {mapActions, mapGetters} from "vuex"
import {Button, DropDown, Modal, Navbar} from '@/components';
import {DatePicker, Popover, Tooltip} from 'element-ui';
import {getDirection} from '@/util';
import {localeOptions} from '@/util'

export default {
  name: 'main-navbar',
  props: {
    transparent: Boolean,
    colorOnScroll: Number
  },
  components: {
    Modal,
    [Button.name]: Button,
    [Popover.name]: Popover,
    [Tooltip.name]: Tooltip,
    [DatePicker.name]: DatePicker,
    DropDown,
    Navbar,
    [Popover.name]: Popover
  },
  data() {
    return {
      modalTransaction: false,
      modalError: false
    };
  },
  computed: {
    ...mapGetters(['errorStarting', 'account', 'transaction', 'web3', 'errorMessage']),
    smallAccount() {
      var lenght = this.account.length
      return this.account.substr(0, 6) + '...' + this.account.substr(lenght-4, lenght)
    }
  },
  methods: {
    ...mapActions(['stopProvider', 'startMetamask', 'startWalletConnect', 'setLang', 'connectionModal', 'getProvider']),
    clickOpenModal: function (){
      this.connectionModal(true)
    },
    clickStopProvider: function () {
      this.stopProvider()
    },
    changeLocale(locale, direction) {
      const currentDirection = getDirection().direction
      if (direction !== currentDirection) {
        setDirection(direction)
      }

      this.setLang(locale)
    },
    langVariants() {
      return localeOptions
    }
  },
  mounted() {
    this.$nextTick(function () {
      window.setInterval(() => {
        this.getProvider()
      },1000);
    })
  },
  watch: {
    transaction: function () {
      this.modalTransaction = true;
    },
    errorMessage: function () {
      this.modalError = true
    }
  }
};
</script>
