//    USDT/QUB
import store from '../../store'
import {uniswapV2PairUsdt} from '@/util/contract/uniswap-v2-pair-usdt'
import {uniswapRewardsUsdt} from '@/util/contract/uniswap-rewards-usdt'
import {maxApproveSum} from '@/util/index'
import app from "@/main";

export default {
    namespaced: true,
    state: {
        balance: 0,
        allowance: 0
    },
    getters: {
        balance: state => state.balance,
        allowance: state => state.allowance,
    },
    mutations: {
        balanceOfSuccess(state, payload) {
            state.balance = payload
        },
        allowanceSuccess(state, payload) {
            state.allowance = payload
        },
        balanceOfError(state, payload) {
            console.log('balanceOf error')
            console.log(payload)
        },
        allowanceError(state, payload) {
            console.log('allowance error')
            console.log(payload)
        },
        disapproveError(state, payload) {
            console.log('disapprove error')
            console.log(payload)
        },
        approveError(state, payload) {
            console.log('approve error')
            console.log(payload)
        }
    },
    actions: {
        getAllowance({commit}) {
            var contract = new store.state.provider.web3.eth.Contract(uniswapV2PairUsdt.abi, uniswapV2PairUsdt.address)

            contract.methods.allowance(store.state.provider.account, uniswapRewardsUsdt.address).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('allowanceError', error)
                }else{
                    commit('allowanceSuccess', data)
                }
            })
        },
        getBalanceOf({commit}) {
            var contract = new store.state.provider.web3.eth.Contract(uniswapV2PairUsdt.abi, uniswapV2PairUsdt.address)

            contract.methods.balanceOf(store.state.provider.account).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('balanceOfError', error)
                }else{
                    commit('balanceOfSuccess', data)
                }
            })
        },
        txDisaprove({commit}) {
            if (store.state.provider.web3 == null) {store.commit('setErrorMessage', app.$i18n.t('general.you-need-connect')); return }

            var contract = new store.state.provider.web3.eth.Contract(uniswapV2PairUsdt.abi, uniswapV2PairUsdt.address)

            contract.methods.approve(uniswapRewardsUsdt.address, 0).send({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    if (error.message != undefined) {
                        store.commit('setErrorMessage',error.message)
                    }else{
                        commit('disapproveError', error)
                    }
                }else{
                    store.commit('transactionCreated', data)
                }
            })
        },
        txApprove({commit}) {
            if (store.state.provider.web3 == null) {store.commit('setErrorMessage', app.$i18n.t('general.you-need-connect')); return }

            var contract = new store.state.provider.web3.eth.Contract(uniswapV2PairUsdt.abi, uniswapV2PairUsdt.address)
            var hexSum = store.state.provider.web3.utils.toWei(maxApproveSum.toString(), 'ether')

            contract.methods.approve(uniswapRewardsUsdt.address, hexSum).send({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    if (error.message != undefined) {
                        store.commit('setErrorMessage',error.message)
                    }else{
                        commit('disapproveError', error)
                    }
                }else{
                    store.commit('transactionCreated', data)
                }
            })
        }
    }
}