import app from '../../main'
import {onConnect} from '../../util/web3Modal'

export default {
    state: {
        errorMessage: '',
        subscribeResult: false,
    },
    getters: {
        errorMessage: state => state.errorMessage,
        subscribeResult: state => state.subscribeResult,
    },
    mutations: {
        setErrorMessage(state, payload) {
            state.errorMessage = payload
        },
        subscribeSuccess(state, payload) {
            state.subscribeResult = payload
        }
    },
    actions: {
        connectionModal({commit}) {
            onConnect(commit)
        },
        subscribe({commit}, email) {
            const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

            if (!re.test(email)) {
                commit('subscribeSuccess', app.$i18n.t('general.email-not-correct') + ' - ' + email)
            }else{
                const myRequest = new Request('https://qubcoins.world/email/?' + email, {method: 'GET', mode: 'no-cors'});

                fetch(myRequest).then(result => {
                    if (result.status) {
                        commit('subscribeSuccess', app.$i18n.t('general.subscribe-success'))
                    } else {
                        //commit('subscribeError', result.data)
                        commit('subscribeSuccess', app.$i18n.t('general.subscribe-success'))
                    }
                })
            }
        }
    }
}