import store from "@/store";
import {qubNqub} from '@/util/contract/qub-nqub'
import app from "@/main";

export default {
    namespaced: true,
    state: {
        withdrawDates: 0,
        balance: 0,
    },
    getters: {
        withdrawDates: state => state.withdrawDates,
        balance: state => state.balance,
    },
    mutations: {
        withdrawDatesSuccess(state, payload) {
            state.withdrawDates = payload
        },
        balanceSuccess(state, payload) {
            state.balance = payload
        },
        withdrawDatesError(state, payload) {
            state.withdrawDates = 0
            console.log('withdrawDates error')
            console.log(payload)
        },
        balanceError(state, payload) {
            state.balance = 0
            console.log('Balance error')
            console.log(payload)
        },
        stakeError(state, payload) {
            console.log('Stake error')
            console.log(payload)
        }
    },
    actions: {
        getWithdrawDates({commit}) {
            var contract = new store.state.provider.web3.eth.Contract(qubNqub.abi, qubNqub.address)

            contract.methods.withdrawDates(store.state.provider.account).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('withdrawDatesError', error)
                }else{
                    commit('withdrawDatesSuccess', data)
                }
            })
        },
        getBalance({ commit }) {
            var contract = new store.state.provider.web3.eth.Contract(qubNqub.abi, qubNqub.address)

            contract.methods.balances(store.state.provider.account).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('balanceError', error)
                }else{
                    commit('balanceSuccess', data)
                }
            })
        },
        txStake({commit}, stackAmount) {
            if (store.state.provider.web3 == null) {store.commit('setErrorMessage', app.$i18n.t('general.you-need-connect')); return }

            var totalAmount = store.state.provider.web3.utils.toWei(stackAmount.toString(), 'ether')

            if (totalAmount < 0) {
                totalAmount = 0
            }

            var contract = new store.state.provider.web3.eth.Contract(qubNqub.abi, qubNqub.address)

            contract.methods.stake(totalAmount).send({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    if (error.message != undefined) {
                        store.commit('setErrorMessage',error.message)
                    }else{
                        commit('stakeError', error)
                    }
                } else {
                    store.commit('transactionCreated', data)
                }
            })
        },
    }
}