import Vue from 'vue'
import Vuex from 'vuex'

import app from '../main'
import contract from './contract'
import qubCoin from './contract/qub-coin'
import qubNqub from './contract/qub-nqub'
import qubPermission from './contract/qub-permission'
import uniswapRewardsEth from './contract/uniswap-rewards-eth'
import uniswapRewardsUsdt from './contract/uniswap-rewards-usdt'
import uniswapV2PairEth from './contract/uniswap-v2-pair-eth'
import uniswapV2PairUsdt from './contract/uniswap-v2-pair-usdt'
import provider from './provider'
import general from './general'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
    },
    mutations: {
        changeLang (state, payload) {
            app.$i18n.locale = payload
            localStorage.setItem('currentLanguage', payload)
        }
    },
    actions: {
        setLang ({ commit }, payload) {
            commit('changeLang', payload)
        }
    },
    modules: {
        namespaced: true,
        contract,
        qubCoin,
        qubNqub,
        qubPermission,
        uniswapRewardsEth,
        uniswapRewardsUsdt,
        uniswapV2PairEth,
        uniswapV2PairUsdt,
        provider,
        general,
    }
})