import store from "@/store";
import {qubPermission} from "@/util/airdrop/qubPermission";
import {qubCoin} from "@/util/contract/qub-coin";

export default {
    namespaced: true,
    state: {
        isClaimed: 0,
        inWhitelist: 0,
        claimedAddressesCount: 0,
        addressesCountInWhitelist: 0,
    },
    getters: {
        isClaimed: state => state.isClaimed,
        inWhitelist: state => state.inWhitelist,
        claimedAddressesCount: state => state.claimedAddressesCount,
        addressesCountInWhitelist: state => state.addressesCountInWhitelist,
    },
    mutations: {
        isClaimedSuccess(state, payload) {
            state.isClaimed = payload
        },
        inWhitelistSuccess(state, payload) {
            state.inWhitelist = payload
        },
        claimedAddressesCountSuccess(state, payload) {
            state.claimedAddressesCount = payload
        },
        addressesCountInWhitelistSuccess(state, payload) {
            state.addressesCountInWhitelist = payload
        },
        isClaimedError(state, payload) {
            state.isClaimed = 0
        },
        inWhitelistError(state, payload) {
            state.inWhitelist = 0
        },
        claimedAddressesCountError(state, payload) {
            state.claimedAddressesCount = 0
        },
        addressesCountInWhitelistError(state, payload) {
            state.addressesCountInWhitelist = 0
        }
    },
    actions: {
        getIsClaimed({ commit }, address) {
            var contract = new store.state.provider.web3.eth.Contract(qubPermission.abi, qubPermission.address)

            contract.methods.isClaimed(address).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('isClaimedError', error)
                } else {
                    commit('isClaimedSuccess', data)
                }
            })
        },
        getInWhitelist({ commit }, address) {
            var contract = new store.state.provider.web3.eth.Contract(qubPermission.abi, qubPermission.address)

            contract.methods.inWhitelist(address).call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('inWhitelistError', error)
                } else {
                    commit('inWhitelistSuccess', data)
                }
            })
        },
        getClaimedAddressesCount({ commit }) {
            var contract = new store.state.provider.web3.eth.Contract(qubPermission.abi, qubPermission.address)

            contract.methods.claimedAddressesCount().call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('claimedAddressesCountError', error)
                } else {
                    commit('claimedAddressesCountSuccess', data)
                }
            })
        },
        getAddressesCountInWhitelist({ commit }) {
            var contract = new store.state.provider.web3.eth.Contract(qubPermission.abi, qubPermission.address)

            contract.methods.addressesCountInWhitelist().call({from: store.state.provider.account}, function (error, data) {
                if (error) {
                    commit('addressesCountInWhitelistError', error)
                } else {
                    commit('addressesCountInWhitelistSuccess', data)
                }
            })
        },
        txClaimContractPermission({ commit }) {
            store.state.provider.web3.eth.contract(qubPermission.abi).at(qubPermission.address).claim({value: web3.toWei(0.05, "ether")}, function (error, data) {
                if (error) {
                    if (error.message != undefined) {
                        store.commit('setErrorMessage',error.message)
                    }else{
                        commit('claimContractError', error)
                    }
                } else {
                    store.commit('transactionCreated', data)
                }
            })
        }
    }
}