<template>
  <div>
    <div class="page-header clear-filter" filter-color="orange">
      <parallax
          class="page-header-image"
          style="background-image:url('/img/field_image_1_805.jpg')"
      >
      </parallax>
      <div class="container" style="max-width: 800px;">
        <div class="row" style="margin-top: 62px">
          <div class="card card-signup gradient--secondary over-hidden" style="max-width: 340px;">
            <div class="card-header text-center" style="padding-bottom: 15px;">
              <h3 class="card-title title-up">Airdrop</h3>
            </div>
            <div class="card-body">
              <p>{{$t('general.pre-mine-airdrop')}}</p>
              <p>{{$t('general.if-you-want-claim')}}</p>
              <div class="card-footer text-center" style="padding-top: 0px;" v-if="isClaimed==false">
                <button class="btn btn-round btn-neutral btn-lg" v-on:click="confirmClaim" style="font-weight: bold; width: 240px; padding-left: 5px; padding-right: 5px;">{{$t('general.get')}} {{claimAmount}} QUB</button>
              </div>
            </div>
          </div>
          <div class="card card-signup gradient--secondary over-hidden" style="max-width: 440px; padding-bottom: 40px;">
            <div class="card-header text-center" style="padding-bottom: 15px; font-weight: bold;">
              {{$t('general.your-address')}}<br/><br/>
              {{account}}
            </div>
            <table style="width: 100%; font-weight: bold;">
              <tr>
                <td style="padding-top: 20px;">{{$t('general.count-address')}}</td>
                <td style="padding-top: 20px;">{{$t('general.recieve-stack')}}</td>
              </tr>
              <tr>
                <td>{{addressesCountInWhitelist}}</td>
                <td>{{claimedAddressesCount}}</td>
              </tr>
              <tr>
                <td style="padding-top: 40px;">{{$t('general.you-are-in-list')}}</td>
                <td style="padding-top: 40px;">{{$t('general.is-claimed')}}</td>
              </tr>
              <tr>
                <td>{{inWhitelist == false ? $t('general.no') : $t('general.yes') }}</td>
                <td>{{isClaimed == false ? $t('general.no') : $t('general.yes') }}</td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {mapActions, mapGetters, mapState} from "vuex";

export default {
  data() {
    return {
      amount: 1000,
      claimAmount: 181.378
    }
  },
  computed: {
    ...mapGetters(['account']),
    ...mapState({
      isClaimed: state => state.qubPermission.isClaimed,
      inWhitelist: state => state.qubPermission. inWhitelist,
      claimedAddressesCount: state => state.qubPermission.claimedAddressesCount,
      addressesCountInWhitelist: state => state.qubPermission.addressesCountInWhitelist,
    }),
  },
  methods: {
    ...mapActions({
      getIsClaimed: 'qubPermission/getIsClaimed',
      getInWhitelist: 'qubPermission/getInWhitelist',
      getClaimedAddressesCount: 'qubPermission/getClaimedAddressesCount',
      getAddressesCountInWhitelist: 'qubPermission/getAddressesCountInWhitelist',
      txClaimContractPermission: 'qubPermission/txClaimContractPermission',
    }),
    confirmClaim: function () {
      if (this.account != undefined) {
        this.claimContractPermission({account: this.account})
      }else{
        this.modalMetamask = true
      }
    },
    confirmApprove: function () {
      this.approveContract({balance: this.balance})
    },
    updateData: function () {
      this.getIsClaimed(this.account)
      this.getInWhitelist(this.account)
      this.getClaimedAddressesCount(this.account)
      this.getAddressesCountInWhitelist(this.account)
    }
  },
  mounted() {
    this.$nextTick(function () {
      window.setInterval(() => {
        this.updateData()
      },5000);
    })
  },
  watch: {
    account: function () {
      this.updateData()
    }
  }
}
</script>