<template>
  <div>
    <div class="page-header clear-filter" filter-color="orange">
      <div style="position: absolute;top: 0;width: 100%;height: 100%;background: url(../img/com-bg-2.jpg) no-repeat;background-size: cover;"></div>
      <vue-particles
          color="#dedede"
          :particleOpacity="0.7"
          :particlesNumber="80"
          shapeType="circle"
          :particleSize="4"
          linesColor="#dedede"
          :linesWidth="1"
          :lineLinked="true"
          :lineOpacity="0.4"
          :linesDistance="150"
          :moveSpeed="3"
          :hoverEffect="true"
          hoverMode="grab"
          :clickEffect="true"
          clickMode="push"
      >
      </vue-particles>
      <div class="container m-w-800px">
        <div class="row m-t-62px">
          <div class="card card-signup gradient--secondary2 m-w-360px">
            <div class="card-header text-center p-b-15px">
              <img src="/img/xdna_logo3.png" style="height: 98px; margin-bottom: 16px;">
              <h3 class="card-title title-up">{{ floorAmount(earned)}}</h3>
            </div>
            <div class="card-body">
              <p>xDNA {{$t('general.earned')}}</p>
              <div class="card-footer text-center">
                <button class="btn btn-round btn-neutral btn-lg btn-get" v-on:click="confirmGetReward" style="width: 140px; padding-left: 5px; padding-right: 5px;">{{$t('general.withdraw')}}</button>
              </div>
            </div>
          </div>
          <div class="card card-signup gradient--secondary over-hidden">
            <div class="card-header text-center">
              <img src="/img/ethereum.png" style="height: 98px;">
              <h3 class="card-title title-up" style="margin-top: 13px;">{{ floorAmount(balance)}}</h3>
            </div>
            <div class="card-body">
              <p>xDNA/ETH LP {{$t('general.staked')}}</p>
              <div class="card-footer text-center">
                <n-button v-if="allowance>0" type="button" class="btn btn-round btn-neutral btn-lg" @click.native="modalDeposit = true" style="width: 140px; padding-left: 5px;padding-right: 5px; margin-left: 5px; margin-right: 5px;">
                  {{$t('general.stake')}}
                </n-button>
                <n-button v-if="allowance>0" type="button" class="btn btn-round btn-neutral btn-lg" @click.native="modalReward = true" style="width: 140px; padding-left: 5px; padding-right: 5px;">
                  {{$t('general.withdraw')}}
                </n-button>
                <n-button v-if="allowance==0" type="button" class="btn btn-round btn-neutral btn-lg" v-on:click="confirmApprove" style="width: 140px; padding-left: 5px; padding-right: 5px;">
                  {{$t('general.approve')}}
                </n-button>
              </div>
            </div>
          </div>
        </div>
        <div class="btns-container">
          <button type="button" class="btn btn-round btn-neutral btn-lg m-t-40px" v-on:click="confirmExit">{{$t('general.unstack-and-withdraw')}}</button>
          <button type="button" class="btn btn-round btn-neutral btn-lg m-t-40px" v-on:click="confirmDisapprove">{{$t('general.disapprove')}}</button>
        </div>
      </div>
    </div>
    <modal :show.sync="modalDeposit" class="modal-primary" :show-close="false" :show-header="false">
      <h4 style="text-align: center; margin-top: 5px;">{{$t('general.deposit')}}</h4>
      <div class="form-group no-border input-lg input-group">
        <p>{{$t('general.available')}}: {{ floorAmount(availableBalance)}} xDNA/ETH LP</p>
        <div class="input-group"><div class="input-group-prepend"><i class="input-group-text now-ui-icons business_money-coins"></i></div>
            <input aria-describedby="addon-right addon-left" :placeholder="$t('general.amount_')" class="form-control" v-model="stackAmount">
            <button class="max-right-button" v-on:click="setMaxStack">Max</button>
        </div>
        <div class="form-group no-border input-lg input-group">
          <button type="button" class="btn btn-round btn-neutral btn-lg w-100" v-on:click="confirmStack" :disabled="(availableBalance!=0 && stackAmount > 0 && stackAmount<=floorAmountClear(availableBalance)) ? false : true">{{$t('general.confirm')}}</button>
          <template v-if="availableBalance!=0">
            <span v-if="stackAmount<=0" style="color: red;">{{$t('general.sum-bolhse-nul')}}</span>
            <span v-if="stackAmount>floorAmountClear(availableBalance)" style="color: red;">{{$t('general.sum-menshe-max')}} {{floorAmount(availableBalance)}}</span>
          </template>
          <template v-else>
            <span v-html="$t('general.you-must-add-liqudity-pool')"></span>
          </template>
        </div>
      </div>
    </modal>
    <modal :show.sync="modalReward" class="modal-primary" :show-close="false" :show-header="false">
      <h4 style="text-align: center; margin-top: 5px;">{{$t('general.withdraw')}}</h4>
      <div class="form-group no-border input-lg input-group">
        <p>{{$t('general.available')}}: {{ floorAmount(balance)}} xDNA/ETH LP</p>
        <div class="input-group">
          <div class="input-group-prepend"><i class="input-group-text now-ui-icons business_money-coins"></i></div>
          <input aria-describedby="addon-right addon-left" :placeholder="$t('general.amount_')" class="form-control" v-model="withdrawAmount">
          <button class="max-right-button" v-on:click="setMaxWithdraw">Max</button>
        </div>
        <div class="form-group no-border input-lg input-group">
          <button type="button" class="btn btn-round btn-neutral btn-lg w-100" v-on:click="confirmWithdraw" :disabled="(balance!=0 && withdrawAmount > 0 && withdrawAmount<=floorAmountClear(balance)) ? false : true">{{$t('general.confirm')}}</button>
          <template v-if="balance!=0">
            <span v-if="withdrawAmount<=0" style="color: red;">{{$t('general.sum-bolhse-nul')}}</span>
            <span v-if="withdrawAmount>floorAmountClear(balance)" style="color: red;">{{$t('general.sum-menshe-max')}} {{floorAmount(balance)}}</span>
          </template>
          <template v-else>
            <span v-html="$t('general.your-balance-is-zero')"></span>
          </template>
        </div>
      </div>
    </modal>
  </div>
</template>
<script>

import {mapActions, mapState} from "vuex";
import {Button, Modal, FormGroupInput} from '@/components'
import {DatePicker, Popover, Tooltip} from "element-ui"
import {mapGetters} from "vuex"
import Web3 from "web3";

export default {
  bodyClass: 'index-page',
  components: {
    Modal,
    [Button.name]: Button,
    [Popover.name]: Popover,
    [Tooltip.name]: Tooltip,
    [DatePicker.name]: DatePicker,
    [FormGroupInput.name]: FormGroupInput
  },
  computed: {
    ...mapGetters(['account']),
    ...mapState({
      earned: state => state.uniswapRewardsEth.earned,
      balance: state => state.uniswapRewardsEth. balance,
      allowance: state => state.uniswapV2PairEth.allowance,
      availableBalance: state => state.uniswapV2PairEth.balance,
    }),
  },
  methods: {
    ...mapActions({
      getEarned: 'uniswapRewardsEth/getEarned',
      getBalanceOf: 'uniswapRewardsEth/getBalanceOf',
      getAllowance: 'uniswapV2PairEth/getAllowance',
      getAllowBalance: 'uniswapV2PairEth/getBalanceOf',
      txDisaprove: 'uniswapV2PairEth/txDisaprove',
      txApprove: 'uniswapV2PairEth/txApprove',
      txGetReward: 'uniswapRewardsEth/txGetReward',
      txExit: 'uniswapRewardsEth/txExit',
      txWithdraw: 'uniswapRewardsEth/txWithdraw',
      txStake: 'uniswapRewardsEth/txStake',
    }),
    confirmDisapprove: function () {
      this.txDisaprove()
    },
    confirmGetReward: function() {
      this.txGetReward({account: this.account})
    },
    confirmApprove: function() {
      this.txApprove()
    },
    confirmStack: function() {
      this.txStake({amount: this.stackAmount})
      this.modalDeposit = false
    },
    confirmExit: function() {
      this.txExit({account: this.account})
    },
    confirmWithdraw: function() {
      this.txWithdraw({amount: this.withdrawAmount})
      this.modalReward = false
    },
    setMaxWithdraw: function () {
      this.withdrawAmount = this.floorAmount(this.balance)
    },
    setMaxStack: function () {
      this.stackAmount = this.floorAmount(this.availableBalance)
    },
    floorAmount: function (amount) {
      if (amount == 0){
        return (Math.floor(amount / 1e12) / 1e6).toFixed(8)
      }else{
        var fullAmount = Web3.utils.fromWei(amount.toString(), 'ether')
        var splittedAmount = (""+fullAmount).split(".");

        if (splittedAmount.length > 1) {
          amount = splittedAmount[0] + '.' + splittedAmount[1].substring(0, 10)
        }else{
          amount = fullAmount + '.00000000'
        }

        return amount
      }
    },
    floorAmountClear: function (amount) {
      return parseFloat(Web3.utils.fromWei(amount.toString(), 'ether'))
    },
    updateData() {
      if (this.account != null){
        this.getEarned(this.account)
        this.getBalanceOf(this.account)
        this.getAllowance(this.account)
        this.getAllowBalance(this.account)
      }
    }
  },
  data() {
    return {
      withdrawAmount: 0.1,
      stackAmount: 0.1,
      modalDeposit: false,
      modalReward: false
    };
  },
  mounted() {
    this.updateData()
    this.$nextTick(function () {
      window.setInterval(() => {
        this.updateData()
      },5000);
    })
  },
  watch: {
    account: function () {
      this.updateData()
    }
  }
};
</script>
<style lang="scss">
#javascriptComponents {
  .modal-buttons,
  .popover-buttons,
  .tooltip-buttons {
    .btn + .btn {
      margin-left: 3px;
    }
  }
}
</style>
